body {
  font-size: 1rem;
  font-family: var(--font-family);
  background-color: #ffffff;
  overflow-x: hidden;
}

.container {
  padding: 0 15px;
  margin-right: auto;
  margin-left: auto;
}

/* Medium devices (tablets, 768px and up) The navbar toggle appears at this breakpoint */
@media only screen and (min-width: 36rem) {
  .container {
    max-width: 540px;
  }
}
@media only screen and (min-width: 48rem) {
  .container {
    max-width: 720px;
  }
}
/* Large devices (desktops, 992px and up) */
@media only screen and (min-width: 62rem) {
  .container {
    max-width: 960px;
  }
}
/* Extra large devices (large desktops, 1200px and up) */
@media only screen and (min-width: 75rem) {
  .container {
    max-width: 1140px;
  }
}
/**
 * Basic typography style for copy text
 */
h2.title {
  font-size: 1.5em;
  font-weight: 500;
  line-height: 2.25rem;
  max-width: 272px;
  color: var(--clr-title);
  margin-inline: 8vw;
}
@media only screen and (min-width: 48rem) {
  h2.title {
    font-size: 3em;
    line-height: 1.5em;
    margin-top: 0.3em;
    max-width: 543px;
  }
}

h3.title {
  font-size: 1.5em;
  line-height: 1.5em;
  font-weight: 500;
  color: var(--clr-title);
  margin-inline: 8vw;
  margin-top: 4em;
  max-width: 260px;
}
@media only screen and (min-width: 48rem) {
  h3.title {
    font-size: 2.25em;
    max-width: 350px;
    margin-inline: 2vw;
  }
}

.card__btn {
  display: inline-block;
  font-size: 1em;
  line-height: 1.5em;
  font-weight: 500;
  color: var(--clr-text2);
  background-color: var(--clr-btn);
  padding: 0.5em 0.7em;
  border-radius: 0.75em;
  transition: all 0.4s ease-in-out;
}

.see__btn {
  display: inline-block;
  font-size: 1.125em;
  line-height: 1.5em;
  font-weight: 500;
  color: var(--clr-blue);
}
.see__btn i {
  display: inline-block;
  margin-left: 1em;
  width: 50px;
}

.toggle-btn {
  position: relative;
  width: 18px;
  height: 12px;
  background-color: inherit;
  cursor: pointer;
}
.toggle-btn span {
  position: absolute;
  display: block;
  width: 100%;
  height: 2px;
  left: 0;
  background-color: var(--clr-title);
  border-radius: 1px;
  pointer-events: none;
}
.toggle-btn span:nth-child(1) {
  top: 0;
}
.toggle-btn span:nth-child(2) {
  top: calc((100% - 2px) / 2);
}
.toggle-btn span:nth-child(3) {
  bottom: 0;
}
.toggle-btn.open span:nth-child(1) {
  animation: 0.5s linear 0s forwards toggle-1;
}
.toggle-btn.open span:nth-child(2) {
  animation: 0.25s linear 0s forwards toggle-2;
}
.toggle-btn.open span:nth-child(3) {
  animation: 0.5s linear 0s forwards toggle-3;
}
.toggle-btn.close span:nth-child(1) {
  animation: 0.5s linear 0s forwards toggle-4;
}
.toggle-btn.close span:nth-child(2) {
  animation: 0.25s linear 0s forwards toggle-5;
}
.toggle-btn.close span:nth-child(3) {
  animation: 0.5s linear 0s forwards toggle-6;
}
@media only screen and (min-width: 62rem) {
  .toggle-btn {
    display: none;
  }
}

@keyframes toggle-1 {
  0% {
    top: 0px;
  }
  50% {
    top: 5px;
    transform: rotate(0deg);
  }
  100% {
    top: 5px;
    transform: rotate(-45deg);
  }
}
@keyframes toggle-2 {
  0% {
    opacity: 1;
  }
  80% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes toggle-3 {
  0% {
    bottom: 0;
  }
  50% {
    bottom: 5px;
    transform: rotate(0deg);
  }
  100% {
    bottom: 5px;
    transform: rotate(45deg);
  }
}
@keyframes toggle-4 {
  0% {
    top: 5px;
    transform: rotate(-45deg);
  }
  50% {
    top: 5px;
    transform: rotate(0deg);
  }
  100% {
    top: 0px;
    transform: rotate(0deg);
  }
}
@keyframes toggle-5 {
  0% {
    opacity: 0;
  }
  80% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes toggle-6 {
  0% {
    bottom: 5px;
    transform: rotate(45deg);
  }
  50% {
    bottom: 5px;
    transform: rotate(0deg);
  }
  100% {
    bottom: 0px;
    transform: rotate(0deg);
  }
}
.contact__form {
  position: relative;
  max-width: 291px;
  margin-top: 2em;
  margin-left: 8vw;
}
.contact__form label {
  display: block;
  font-size: 0.625em;
  font-weight: 500;
  line-height: 1.5em;
  color: var(--clr-text2);
}
.contact__form .email {
  display: block;
  width: 100%;
  margin-top: 0.5em;
  padding: 1em 1em;
  border: 0;
  outline: 0;
  background-color: var(--clr-input);
  border-radius: 0.75em;
}
.contact__form .email::placeholder {
  transform-origin: left;
  transition: transform 0.3s ease-in-out, opacity 0.3s linear;
}
.contact__form .email:focus-visible::placeholder, .contact__form .email:focus::placeholder {
  transform: scale(0);
  opacity: 0;
}
.contact__form .form__btn {
  font-size: 0.875rem;
  font-weight: 500;
  position: absolute;
  border: 0;
  outline: 1px solid var(--clr-blue);
  outline-offset: -1px;
  bottom: 25px;
  right: 4px;
  transform: translateY(50%);
  padding: 0.9em 1.4em;
  border-radius: 0.75em;
  color: var(--clr-input);
  background-color: var(--clr-blue);
  cursor: pointer;
  transition: all 0.3s linear;
}
.contact__form .form__btn:hover, .contact__form .form__btn:focus-visible {
  color: var(--clr-blue);
  background-color: var(--clr-input);
}
@media only screen and (min-width: 62rem) {
  .contact__form {
    max-width: 350px;
    margin-top: 5em;
  }
  .contact__form label {
    font-size: 0.875em;
  }
  .contact__form .form__btn {
    font-size: 1.125rem;
    padding: 0.55em 1.4em;
  }
}

.header {
  position: relative;
  background-color: #fff;
}
.header .container {
  padding: 1em 15px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.header .container .logo {
  margin-right: auto;
}
.header .container .logo h1 {
  font-family: var(--font-family-logo);
  font-size: 1.5em;
  color: var(--clr-title);
  margin: 0;
  cursor: pointer;
}
@media only screen and (min-width: 62rem) {
  .header .container .logo h1 {
    font-size: 2.25em;
  }
}

.nav {
  position: absolute;
  top: 100%;
  right: 0vw;
  z-index: 100;
  width: 0vw;
  height: 100vh;
  background-color: var(--clr-footer);
  transform: scaleX(0);
  transform-origin: right;
  transition: width 0.3s ease-in-out, opacity 0.3s linear, transform 0.3s linear;
  opacity: 0;
}
.nav.active {
  transform: scaleX(1);
  width: 50vw;
  opacity: 1;
}
.nav.active::before {
  opacity: 0.5;
  width: 50vw;
}
.nav::before {
  content: "";
  position: absolute;
  height: 100vh;
  left: -100%;
  top: 0;
  z-index: 100;
  background-color: var(--clr-text3);
  opacity: 0;
  transition: all 0.3s linear;
}
.nav__list {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.nav__list-item {
  margin-block: min(6vh, 2em);
  margin-inline: 0;
}
.nav__list-item-link {
  color: #fff;
  transition: color 0.2s linear;
  width: max-content;
}
.nav__list-item-link:hover, .nav__list-item-link:focus-visible {
  color: var(--clr-blue);
}
@media only screen and (min-width: 62rem) {
  .nav {
    position: unset;
    width: unset;
    height: unset;
    background-color: inherit;
    opacity: 1;
    transform: scaleX(1);
  }
  .nav.active {
    transform: unset;
  }
  .nav__list {
    flex-direction: unset;
  }
  .nav__list-item {
    margin: 0;
  }
  .nav__list-item:not(:first-of-type) {
    margin-left: 1.8em;
  }
  .nav__list-item-link {
    display: block;
    font-weight: 500;
    font-size: 1.1em;
    padding: 0.2em 0.5em;
    color: var(--clr-title);
  }
}

.hero__text {
  font-size: 0.75em;
  font-weight: 500;
  line-height: 2.25rem;
  color: var(--clr-blue);
  margin-top: 4em;
  margin-left: 8vw;
}
.hero__title {
  font-size: 1.5em;
  font-weight: 500;
  line-height: 2.25rem;
  max-width: 272px;
  color: var(--clr-title);
  margin-inline: 8vw;
}
.hero__img {
  margin-top: 1em;
  border-radius: 1em;
  overflow: hidden;
}
.hero__img img {
  aspect-ratio: 2.38;
}
@media only screen and (min-width: 48rem) {
  .hero__text {
    font-size: 1.125em;
    line-height: 1.6875rem;
    margin-top: 6em;
  }
  .hero__title {
    font-size: 3em;
    line-height: 1.5em;
    margin-top: 0.3em;
    max-width: 543px;
  }
  .hero__img {
    margin-top: 2.625em;
  }
}

.story {
  margin-top: 3em;
  margin-bottom: 5em;
}
.story h2.title {
  max-width: 227px;
}
@media only screen and (min-width: 48rem) {
  .story h2.title {
    max-width: 366px;
  }
}
.story__text {
  font-size: 0.75em;
  font-weight: 500;
  line-height: 2.25em;
  color: var(--clr-text1);
  margin-top: 2em;
  margin-left: 8vw;
  max-width: 273px;
}
@media only screen and (min-width: 48rem) {
  .story__text {
    font-size: 1.125em;
    line-height: 1.6875rem;
    margin-top: 1.5em;
    max-width: 366px;
  }
}

.services__list {
  margin-top: 4em;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(340px, 1fr));
  gap: 1em;
}
.services__list-item {
  position: relative;
  padding: 2em;
  cursor: pointer;
  border-radius: 1.5em;
  margin-bottom: 2em;
}
.services__list-item .icon {
  font-size: 1.5em;
  width: 67px;
  aspect-ratio: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 16px;
  color: #fff;
  margin-bottom: 1.5em;
  background-color: var(--clr-card);
}
.services__list-item:hover {
  --clr-btn: var(--clr-card);
  --clr-text2: #fff;
}
.services__list-item:hover::before {
  opacity: 1;
}
.services__list-item:nth-child(1) {
  --clr-card: var(--clr-blue);
}
.services__list-item:nth-child(2) {
  --clr-card: var(--clr-green);
}
.services__list-item:nth-child(3) {
  --clr-card: var(--clr-red);
}
.services__list-item h4 {
  font-size: 1.5em;
  font-weight: 500;
  line-height: 1.5em;
  color: var(--clr-title);
  margin-bottom: 1.5em;
}
.services__list-item p {
  font-size: 1em;
  line-height: 1.5em;
  color: var(--clr-text1);
  margin-bottom: 2.25em;
}
.services__list-item .card__btn {
  margin-bottom: 1em;
}
.services__list-item::before {
  content: "";
  position: absolute;
  inset: 0;
  border-radius: inherit;
  background-color: transparent;
  box-shadow: 0px 10px 30px rgba(51, 51, 51, 0.1);
  opacity: 0;
  transition: opacity 0.4s linear;
}
@media only screen and (min-width: 48rem) {
  .services__list {
    margin-inline: 2vw;
  }
}

.works {
  margin-top: 1em;
}
.works .title {
  max-width: 267px;
  margin-bottom: 3em;
}
@media only screen and (min-width: 48rem) {
  .works .title {
    max-width: 450px;
  }
}
.works__list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  gap: 2em;
  justify-content: center;
  align-items: center;
}
.works__list-item .item__img {
  max-width: 100%;
  border-radius: 1em;
  overflow: hidden;
  margin-bottom: 1em;
}
.works__list-item .item__img img {
  width: 100%;
}
.works__list-item p {
  font-size: 0.75em;
  line-height: 1.5em;
  font-weight: 300;
  color: var(--clr-text2);
}
.works__list-item h4 {
  font-size: 1.125em;
  line-height: 1.5em;
  font-weight: 500;
  margin-bottom: 1em;
}
.works .see__btn {
  margin-block: 0.5em;
  transition: color 0.2s linear;
}
.works .see__btn:hover {
  --clr-blue: var(--clr-red);
}
@media only screen and (min-width: 62rem) {
  .works__list-item:nth-child(2) {
    margin-top: -12em;
  }
  .works__list-item:nth-child(4) {
    margin-top: -12em;
  }
  .works .see__btn {
    float: right;
    margin-top: -3em;
    margin-right: 5em;
  }
}

.team {
  margin-top: 10em;
  margin-bottom: 10em;
}
.team__contant p:nth-child(1) {
  font-size: 1.125em;
  line-height: 1.5em;
  font-weight: 500;
  color: var(--clr-red);
}
.team__contant h4 {
  font-size: 2.25em;
  line-height: 1.5em;
  font-weight: 500;
  color: var(--clr-title);
  margin-right: 1em;
  margin-block: 0.1em 0.3em;
  max-width: 280px;
}
