  --clr-link: #fff;
  font-size: 1.125em;
  line-height: 1.5em;
  font-weight: 400;
  color: var(--clr-link);
  transition: color 0.25s linear;
  width: max-content;
}
.footer__nav-link:hover {
  --clr-link: var(--clr-blue);
}
